import React, { useState } from 'react';
import '../CSS/SignUp_Modal.css';
import { InsertUser } from '../backend/SignUp'; //
interface SignUpModalProps {
  onClose: () => void;
}

const SignUp_Modal: React.FC<SignUpModalProps> = ({onClose}) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [signupError, setSignupError] = useState(false);
  const [inputError, setInputError] = useState<string | null>(null);

  const handleSignUp = async (e: React.FormEvent) => {
    e.preventDefault();

    // 
    const alphanumericRegex = /^[a-zA-Z0-9]+$/;
    let entryflag=true;
    if (!alphanumericRegex.test(username) || !alphanumericRegex.test(password)) {
      setInputError('/');
      entryflag=false;
      return;
    }


    // 
    if (password !== confirmPassword) {
      setInputError('');
      entryflag=false
      return;
    }
    try {
      const success = await InsertUser(username, password);
      if (success) {
        setInputError(null);
        onClose(); 
      } else {
        setInputError(null);
      }
    } catch (error) {
      console.error('Login error:', error);
      setInputError('');
    }
  };
        /*** 
        if (username == 'user' && password == 'pass' && confirmPassword == 'pass') {
          alert('');
          setInputError(null);
          onClose();
        } else {
          setInputError(null);
        }
        }
        ***/
 

  return (
    <div className="modal-content">
      <div className="modal-header">
        <h2 className="modal-title"></h2>
      </div>
      <form onSubmit={handleSignUp} className="signup-form">
        <label>
          ID:
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="input-field"
          />
        </label>
        <label>
          :
          <input
            type="text"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="input-field"
          />
        </label>
        <label>
          :
          <input
            type="text"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            className="input-field"
          />
        </label>
        {inputError && (
          <div className="error-message">{inputError}</div>
        )}
        <button type="submit" className="signup-button"></button>
      </form>
    </div>
  );
};

export default SignUp_Modal;
