import React, { useState } from 'react';
import '../CSS/SignIn_Modal.css'; 
import {Login} from '../backend/Login';
//import { getAllTodos } from '../../../backend/db.ts'; 

interface SignInModalProps {
  openSignUpModal: () => void;
  onClose: () => void;
  setUser: (user: string | null) => void;
  closeDrawer: () => void;
}

const SignInModal: React.FC<SignInModalProps> = ({ openSignUpModal, onClose, setUser,closeDrawer }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [loginError, setLoginError] = useState(false);
  const [inputError, setInputError] = useState<string | null>(null);
//
  const handleLogin = async(e: React.FormEvent) => {
    e.preventDefault();     //
    // 
    const alphanumericRegex = /^[a-zA-Z0-9]+$/;

    if (!alphanumericRegex.test(username) || !alphanumericRegex.test(password)) {
      setInputError('/');
      return;
    }


    try {
      const success = await Login(username, password);
      
      if (success) {
        setInputError(null);
        setUser(username);     //userstatus
        onClose(); 
        closeDrawer();
        alert('');
      } else {
        setInputError(null);
        setInputError('');
      }
    } catch (error) {
      console.error('Login error:', error);
      setInputError('');
    }
  };




  return (
    <div className="modal-content">
      <div className="modal-header">
        <h2 className="modal-title"></h2>
      </div>
      <form onSubmit={handleLogin} className="login-form">
        <label>
          :
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="input-field"
          />
        </label>
        <label>
          :
          <input
            type="text"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="input-field"
          />
        </label>
        {inputError && (
          <div className="error-message">{inputError}</div>
        )}
        <button type="submit" className="signup-button"></button>
        <div className="signup-link">
          <span><a href="#" onClick={(e) => { e.preventDefault(); openSignUpModal(); }}>Sign Up</a></span>
        </div>
      </form>
    </div>
  );
};

export default SignInModal;
